.widgetLg {
    flex: 2;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, .1);
    padding: 20px;
}

.widgetLgTitle {
    font-size: 20px;
    font-weight: 600;
}

.widgetLgTable {
    width: 100%;
    border-spacing: 20px;
}

.widgetLgTh{
    text-align: left;
}

.widgetLgUser {
    display: flex;
    align-items: center;
    font-weight: 600;
}

.widgetLgImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.widgetLgData,.widgetLgAmount,.widgetLgStatus {
    font-weight: 300;
}

.widgetLgButton {
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}

.widgetLgButton.Approved {
    background: #e5faf2;
    color: #3bb077;
}

.widgetLgButton.Declined {
    background: #fff0f1;
    color: #d95087;
}

.widgetLgButton.Pending {
    background: #ebf1fe;
    color: #2a7ade;
}