.productList {
    flex: 4;
}

.productListItem {
    display: flex;
    align-items: center;
}

.productListImg {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 5px;
}

.productListEdit {
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background: #3bb077;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}

.productListDelete {
    color: red;
    cursor: pointer;
}